import groq from './groqClient';
import { handleAIError, createFallbackResponse, logger } from './errorHandler';

/**
 * Analyzes a financial question and provides insights
 * @param {string} question - The user's financial question
 * @param {number} mood - User's emotional state (1-10)
 * @param {string[]} tags - Contextual tags
 * @returns {Promise<object>} Analysis results
 */
export async function analyzeFinancialQuestion(question, mood = 5, tags = []) {
  try {
    const prompt = `En tant qu'expert en psychologie financi√®re et neurosciences, analysez cette question financi√®re:

Question: "${question}"
√âtat √©motionnel (1-10): ${mood}
Contexte: ${tags.join(', ')}

Fournissez une analyse structur√©e qui inclut:
1. Les d√©clencheurs psychologiques identifi√©s
2. Les patterns comportementaux observ√©s
3. Des recommandations personnalis√©es
4. Une cat√©gorisation de la question

R√©pondez en fran√ßais avec un ton bienveillant et professionnel.`;

    const response = await groq.chat.completions.create({
      model: 'llama-3.1-70b-versatile',
      messages: [
        { 
          role: 'system', 
          content: 'Vous √™tes un expert en psychologie financi√®re et neurosciences comportementales. Vous aidez les utilisateurs √† comprendre leurs habitudes financi√®res.' 
        },
        { role: 'user', content: prompt },
      ],
      temperature: 0.7,
      max_tokens: 1000,
    });

    return {
      analysis: response.choices[0].message.content,
      category: extractCategory(response.choices[0].message.content),
      confidence: 0.85,
      insights: extractInsights(response.choices[0].message.content)
    };
  } catch (error) {
    const handledError = handleAIError(error);
    
    // Return fallback response for better user experience
    if (handledError.code === 'NETWORK_ERROR' || handledError.code === 'GENERIC_ERROR') {
      return createFallbackResponse('analysis');
    }
    
    throw handledError;
  }
}

